.support-ukraine-block {
  // height: 474px;
  width: 356px;
  background: radial-gradient(circle at center top, #4f2ee8, #f5f5f71a);
  padding-top: 24px;
  padding-bottom: 24px;
  border-radius: 3%;
}
.support-ukraine-container__inner {
  overflow: hidden;
  height: 305px;
}
#show-more-button {
  fill: #4f2ee8;
  padding: 0;
}
.support-ukraine-list,
#show-more-button svg {
  transition: 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.expanded .support-ukraine-list {
  transform: translateY(-155px);
}

.expanded #show-more-button svg {
  transform: rotate(180deg);
}
.support-ukraine-item:first-child {
  margin-top: 0;
}
.support-head {
  font-family: DM Sans;
  font-size: 24px;
  font-weight: 700;
  line-height: 28px;
  letter-spacing: -0.04em;
  text-align: left;
  color: #ffffff;
  margin-bottom: 40px;
  margin-left: 40px;
}
.support-ukraine-item {
  font-family: DM Sans;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  letter-spacing: -0.04em;
  text-align: left;
  color: #ffffff;
  margin-top: 20px;
  margin-left: 40px;
  display: flex;
  align-items: center;
  height: 32px;
}
.support-ukraine-item:first-child {
  margin-top: 0;
}

.circle {
  background-color: #fff;
  border-radius: 50%;
  width: 38px;
  height: 38px;
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  margin-top: 24px;
  margin-bottom: 0px;
}

.icon-emblem {
  fill: #ffffff;
  width: 20px;
  height: 32px;
  margin-left: 12px;
  vertical-align: middle;
}
.icon-direction {
  width: 20px;
  height: 20px;
}

.support-ukraine-img {
  filter: brightness(0) invert(1);
  margin-left: 16px;
  max-width: 100%;
  height: 32px;
}
.seventh {
  margin-top: 18px;
}
.third-image {
  height: 15px;
  filter: brightness(0) invert(1);
  margin-left: 16px;
  max-width: 100%;
}
.hidden {
  display: none;
}
@media screen and (min-width: 768px) {
  .support-ukraine-block {
    width: 335px;
  }
}
@media screen and (max-width: 1439px) {

  #shopping-list .support-ukraine-block {
    display: none;
  }


}
@media screen and (max-width: 767px) {
  .support-ukraine-block {
    width: 335px;
  }
  .support-ukraine-container__inner {
    overflow: hidden;
    height: 188px;
  }
  .expanded-second .support-ukraine-list {
    transform: translateY(-200px);
  }

  .expanded-second #show-more-button svg {
    transform: rotate(0deg);
  }
  .expanded-third .support-ukraine-list {
    transform: translateY(-400px);
  }
  .expanded-third #show-more-button svg {
    transform: rotate(0deg);
  }
}




